@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xs);

    width: 100%;
}

.cover {
    width: 100%;

    @include aspectRatio(3, 2);
}

.meta {
    display: flex;
    flex-direction: column;

    width: 100%;
    gap: var(--ym-spacer-size-xxxs);
}

.heading {
    width: 100%;
    max-width: 205px;
    height: 100%;
    min-height: 16px;
}

.title {
    width: 100%;
    max-width: 100px;
    height: 100%;
    min-height: 16px;
}

.subtitle {
    width: 100%;
    max-width: 235px;
    height: 100%;
    min-height: 16px;
}
