@mixin calculateCarouselItemWidth($viewportWidth, $count, $hideOther: true) {
    @if $hideOther {
        @include mediaBreakpointDown($width: $viewportWidth - 1) {
            &:nth-child(n + #{$count}) {
                display: none;
            }
        }
    }

    @include mediaBreakpointUp($width: $viewportWidth) {
        $spacersTotalWidth: calc(var(--ym-spacer-size-l) * ($count - 1));
        width: calc((100% - #{$spacersTotalWidth}) / #{$count});
    }
}
