@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: space-between;

    border-radius: var(--ym-radius-size-xl);

    background-color: var(--ym-background-color-primary-enabled-popover);

    padding: var(--ym-spacer-size-xxl);

    min-height: 192px;
}

.actions {
    display: flex;
    z-index: 1;
    flex-direction: column;
    gap: var(--ym-spacer-size-xxl);

    width: 50%;
}

.textContainer {
    display: flex;
    flex: 1;
    flex-direction: column;
    gap: var(--ym-spacer-size-xxxs);

    max-width: 100%;
}

.title {
    color: var(--ym-controls-color-primary-text-enabled_variant);
}

.imageContainer {
    position: absolute;

    inset-block-start: 50%;
    inset-inline-end: 10%;

    transform: translateY(-50%);

    max-width: 50%;
    height: 110%;

    @include aspectRatio(1, 1);

    @include mediaBreakpointDown($ym-breakpoint-m, false) {
        inset-inline-end: 5%;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        inset-inline-end: 0;
    }
}

.image {
    width: 100%;
    height: 100%;
}

.button {
    padding-block-start: var(--ym-spacer-size-m);
    padding-block-end: var(--ym-spacer-size-m);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-xxl);

    width: fit-content;
}
