@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';
@import '~@yandex-music-int/sol/mixins/calculateCarouselItemWidth.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    &:hover {
        .controls {
            opacity: 1;
        }
    }
}

.content {
    padding-inline-start: var(--ym-spacer-size-xxl);
    scroll-padding-inline-start: var(--ym-spacer-size-xxl);

    scrollbar-gutter: auto;

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        padding-inline-start: var(--ym-spacer-size-l);
        scroll-padding-inline-start: var(--ym-spacer-size-l);
    }
}

.item {
    width: 100%;

    @include mediaBreakpointDown(1240) {
        width: 40%;
    }

    @include mediaBreakpointDown(1100) {
        width: 45%;
    }

    @include mediaBreakpointDown(960) {
        width: 52%;
    }

    @include mediaBreakpointDown(860) {
        width: 54%;
    }

    @include mediaBreakpointDown(830) {
        width: 64%;
    }

    @include mediaBreakpointDown(600) {
        width: 60%;
    }

    @include mediaBreakpointDown(540) {
        width: 65%;
    }

    @include mediaBreakpointDown(480) {
        width: 73%;
    }

    @include mediaBreakpointDown(440) {
        width: 80%;
    }

    @include mediaBreakpointDown(410) {
        width: 100%;
    }

    @include calculateCarouselItemWidth(1240, 3, false);
    @include calculateCarouselItemWidth(1550, 3, false);
    @include calculateCarouselItemWidth(1920, 3, false);
    @include calculateCarouselItemWidth(2560, 4, false);
}
