.root {
    display: flex;

    flex-direction: column;
    align-items: center;

    margin-block-start: var(--ym-spacer-size-l);
    margin-block-end: var(--ym-spacer-size-l);

    text-align: center;
}

.honeycomb {
    display: flex;
    flex-direction: column;
    align-items: center;

    &_top {
        position: relative;
        inset-block-start: var(--ym-spacer-size-xxxs);
    }

    &_bottom {
        position: relative;
        inset-block-end: var(--ym-spacer-size-xxxs);
    }

    &_margin {
        margin-block-end: var(--ym-spacer-size-xxxl);
    }
}

.title {
    margin-block-end: var(--ym-spacer-size-m);

    color: var(--ym-controls-color-primary-text-enabled_variant);
}

.description {
    margin-block-end: var(--ym-spacer-size-xxl);

    width: 85%;
    max-width: 440px;
}

.button {
    font-size: var(--ym-font-size-label-m);
}

.buttonIcon {
    fill: var(--ym-controls-color-secondary-on_default-enabled_variant);
}

.honeycombLine {
    display: flex;
    gap: var(--ym-spacer-size-xxxs);
}

.honeycombCell {
    display: flex;
    flex-grow: 0;
    align-items: center;
    justify-content: center;

    background-color: var(--ym-controls-color-secondary-default-enabled);

    width: 100%;
    min-width: 64px;
    height: 100%;
    min-height: 64px;

    &_active {
        background-color: var(--ym-controls-color-primary-default-enabled);
    }
}

.honeycombCellIcon {
    color: var(--ym-controls-color-secondary-on_outline-disabled);

    &_active {
        color: var(--ym-controls-color-primary-on_default-enabled);
    }
}
