@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/firefoxStyles.scss';

.root {
    display: flex;
    flex-direction: column;

    padding: 0;
    gap: var(--ym-spacer-size-s);

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        gap: var(--ym-spacer-size-xs);
    }
}

.tab {
    scroll-snap-align: start;
    list-style-type: none;

    flex-shrink: 0;

    max-width: fit-content;
    height: 100%;
}

.tabCarousel {
    scroll-padding-inline-start: var(--ym-spacer-size-xxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        scroll-padding-inline-start: var(--ym-spacer-size-xxl);
    }
}

.container {
    /* Почему здесь используется !important - смотри здесь MFRONT-5960 */

    margin: 0 !important;
    padding-inline-start: var(--ym-spacer-size-xxl) !important;

    @include firefoxStyles() {
        padding-inline-end: var(--ym-spacer-size-l) !important;
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-l) !important;
    }
}
