@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';
@import '~@yandex-music-int/sol/mixins/safariStyles.scss';

.root {
    --new-releases-card-hover-background: linear-gradient(0deg, rgba(255, 255, 255, 0.12), rgba(255, 255, 255, 0.12)),
        var(--new-releases-card-background, var(--ym-background-color-primary-enabled-popover));

    overflow: hidden;
}

.image {
    will-change: transform;

    transition: transform var(--ym-duration-transition) ease-in-out;

    width: 100%;

    @include aspectRatio(1, 1);

    @include safariStyles() {
        will-change: auto;
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.cover {
    display: block;

    position: relative;

    margin-block-end: calc(-1 * var(--new-releases-cover-margin));

    margin-inline-start: auto;
    margin-inline-end: auto;

    max-width: var(--new-releases-cover-size);

    overflow: visible;

    @media (any-hover: hover) {
        cursor: pointer;

        &:hover,
        &:focus-visible {
            .image {
                transform: scale(1.08);
            }

            & + .card {
                background: var(--new-releases-card-hover-background);
            }
        }
    }
}

.fade {
    position: absolute;

    inset-block-start: 0;
    inset-inline-start: 0;

    width: 100%;
    height: 100%;
}

.artists {
    position: absolute;

    margin-inline-start: auto;
    margin-inline-end: auto;

    max-width: 70%;

    text-align: center;
    line-height: var(--ym-font-line-height-headline-m);

    color: var(--ym-controls-color-secondary-text-enabled_variant);

    font-size: var(--ym-font-size-headline-m);

    inset-block-end: calc(var(--new-releases-cover-margin) + var(--ym-spacer-size-xs));
    inset-inline-start: 0;
    inset-inline-end: 0;
}

.artistLink {
    color: inherit;
}

.artistCaption {
    line-height: var(--ym-font-line-height-headline-m);
    font-size: var(--ym-font-size-headline-m);
}

.card {
    background: var(--new-releases-card-background, var(--ym-background-color-primary-enabled-popover));

    @media (any-hover: hover) {
        &:hover {
            background: var(--new-releases-card-hover-background);
        }

        &:has(:focus-visible) {
            background: var(--new-releases-card-hover-background);
        }
    }
}
