@import '~@yandex-music-int/sol/mixins/rippleEffect.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';
@import '~@yandex-music-int/sol/mixins/focusOutline.scss';
@import '~@yandex-music-int/sol/mixins/safariStyles.scss';

.root {
    --new-promotions-zoom-duration: 150ms;
    display: flex;
    flex-direction: column;
    gap: var(--ym-spacer-size-xs);
}

.image {
    will-change: transform;

    transition: transform var(--new-promotions-zoom-duration) ease-in-out;

    width: 100%;

    @include aspectRatio(3, 2);

    @include safariStyles() {
        will-change: auto;
    }

    @media (prefers-reduced-motion: reduce) {
        transition: none;
    }
}

.card {
    @media (any-hover: hover) {
        &:hover {
            .image {
                transform: scale(1.02);
            }
        }
    }
}

.heading {
    color: var(--ym-controls-color-primary-default-hovered);
}

.titleLink {
    color: var(--ym-controls-color-primary-text-enabled_variant);

    &:hover,
    &:focus-visible {
        text-decoration: underline;
    }
}

.subtitle {
    color: var(--ym-controls-color-primary-text-enabled);
}
