@import '~@yandex-music-int/sol/mixins/rippleEffect.scss';
@import '~@yandex-music-int/sol/mixins/breakpoints.scss';
@import '~@yandex-music-int/sol/mixins/aspectRatio.scss';

.root {
    --new-releases-card-cover-size: 64px;

    display: flex;
    position: relative;

    align-items: center;
    justify-content: flex-start;
    column-gap: var(--ym-spacer-size-xs);

    border: 1px solid var(--ym-outline-color-primary-disabled);

    cursor: pointer;

    padding: var(--ym-spacer-size-xs);

    height: var(--new-releases-card-height);

    overflow: hidden;

    @include rippleEffect(var(--ym-controls-color-secondary-default-pressed));
}

.image {
    width: 100%;

    @include aspectRatio(1, 1);
}

.coverLink {
    flex-shrink: 0;
}

.cover {
    max-width: var(--new-releases-card-cover-size);

    overflow: hidden;
}

.description {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

    overflow: hidden;
}

.type {
    width: 100%;

    color: var(--ym-controls-color-secondary-text-enabled);
}

.titleLink {
    width: 100%;

    @media (any-hover: hover) {
        &:hover,
        &:focus {
            .title {
                color: var(--ym-controls-color-secondary-text-hovered);
            }
        }
    }
}

.title {
    width: 100%;

    word-break: break-word;

    color: var(--ym-controls-color-secondary-text-enabled_variant);

    @include mediaBreakpointDown($ym-breakpoint-xxxs) {
        -webkit-line-clamp: 1 !important;
    }
}

.date {
    width: 100%;

    color: var(--ym-controls-color-secondary-text-enabled);
}

.container {
    display: flex;
    flex-shrink: 0;
    align-items: center;
    column-gap: var(--ym-spacer-size-xs);
}

.explicitMark {
    flex-shrink: 0;

    color: var(--ym-controls-color-secondary-text-enabled_variant);
}

.button {
    flex-shrink: 0;

    backdrop-filter: unset;

    will-change: transform;

    margin-inline-end: var(--ym-spacer-size-xxxs);

    &:hover,
    &:focus-visible {
        transform: scale(1.05);
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs) {
        margin-inline-start: 0;
        margin-inline-end: 0;
    }
}
